<template>
  <div>
    <div class="l_top">
      <el-row>
        <el-button
          type="primary"
          @click="addClaim()"
          style="margin-top:20px;margin-left:50px"
        >新建理赔申请单</el-button>
      </el-row>
    </div>
    <el-table
      ref="multipleTable"
      :data="tableData3"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column label="理赔单号" width="120">
        <template slot-scope="scope">{{ scope.row.claimNum }}</template>
      </el-table-column>
      <el-table-column prop="member.mid" label="客户编号" show-overflow-tooltip></el-table-column>
      <el-table-column prop="policyOrder.policyNum" label="保单编号" show-overflow-tooltip></el-table-column>
      <el-table-column prop="applicateTime" label="申请时间" show-overflow-tooltip>
        <template slot-scope="scope">
          {{ scope.row.applicateTime | timeFormat1 }}
        </template>
      </el-table-column>
      <el-table-column prop="project" label="理赔项目"></el-table-column>
      <el-table-column prop="payStyle" label="理赔方式"></el-table-column>
      <el-table-column prop="status" label="状态"></el-table-column>
      <el-table-column label="操作" width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page="page"
      :page-size="size"
      layout="total, prev, pager, next, jumper"
      :total="totalElements"
    ></el-pagination>
  </div>
</template>

<script>
import vueFilter  from '../../utils/filter.js';
export default {
  data() {
    return {
      page: 0,
      size: 10,
      totalElements: 1,
      tableData3: [
        {
          claimNum: "",
          mid: "",
          orderNum: "",
          appTime: "",
          project: "",
          payStyle: "",
          status: ""
        }
      ]
    };
  },

  methods: {
    addClaim() {
      this.$router.push("/claim/detail");
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },

    handleEdit(index, row) {
      console.log("测试 获取ID", row.id);
      this.$router.push({
        path: "/claim/detail",
        query: { id: row.id }
      });
    },
    list(){
      var that=this
      this.$get('/claimapplications',
        {
          params:{
            page: that.page,
            size: that.size
          }
      },).then(function(res){
        console.log(res.data)
        that.tableData3=res.data.content;
        that.page = res.data.pageable.pageNumber + 1;
        that.totalElements = res.data.totalElements;
    }).catch(function (error) {
      console.log(error);
      });
    },
    handleCurrentChange(val) {
      this.page = val;
      this.list();
    },
    handleDelete(index, row) {
      console.log(index, row);
    },

  },
  mounted() {
    this.list();
  }
};
</script>


<style>
.l_top {
  height: 80px;
}
</style>



